<template>
    <div class="drugstore_wrap">
        <GuList msg="지역구를 선택하세요" name="GuList" @setGuList="setGuList" />
        <!-- <div>DrugStore가 자식GuList 에게 전달받은 데이터 : {{ GuNum }} {{ GuName }} {{ GuLink }}</div> -->
        <DrugStoreList :GuNum='GuNum' :GuName='GuName' :GuLink='GuLink'></DrugStoreList>
    </div>
</template>

<script>
import GuList from '@/components/GuList.vue'
import DrugStoreList from '@/components/DrugStoreList.vue'

export default {
    name: 'DrugStore',
    props: {
    },
    data(){
        return{
            DrugStoreList : DrugStoreList,
            GuNum : 0,
            GuName : '강남구',
            GuLink : 'gangnam'
        }
    },
    components: {
        GuList,
        DrugStoreList
    },
    methods:{
        setGuList(value){
            this.GuNum = value[0];
            this.GuName = value[1];
            this.GuLink = value[2];
            console.log('자식 컴포넌트에게 값을 받았어요', [value[0], value[1], value[2]]);
        }
    },
    
}
</script>