<template>
  <div class="gu_wrap" :class="{'index': $route.path == '/'}">

    <div class="gulist">
      <h2><i class="fa-solid fa-map-pin"></i> {{ msg }}</h2>
      <ul>
        <li 
          v-for="(item, ind) in GuList" :key="ind" 
          :class="{'active': currentTab == ind }">
            <button v-on:click="[changeGu(ind, $event)]">{{ item.name }}</button>
        </li>
      </ul>
    </div>
    <!-- <div>보낼 값 : {{ GuNum }} {{ GuName }} {{ GuLink }}</div> -->

  </div>
</template>


<script>
import GuList from '../json/gu.json'

export default {
  name: 'GuList',
  props: {
    msg: String,
    GuCnt : Number,
    name : String
  },
  data(){
    return{
      GuList : GuList,
      currentTab : 0,
      GuNum : 0,
      GuName : "",
      GuLink : ""
    }
  },
  methods: {
    changeGu: function(ind){
      
      this.GuNum = ind;
      this.GuName = this.GuList[ind].name;
      this.GuLink = this.GuList[ind].link;

      this.currentTab = ind;

      // console.log(event.currentTarget); // 해당 button
      // console.log(this.name, '버튼 클릭');  // GuList 버튼 클릭
      this.$emit('setGuList', [this.GuNum, this.GuName, this.GuLink]); // setInput 보냄

      // this.$router.push('/drugstore');

    }
  }
}
</script>




