  .gu_wrap{ margin:0; padding:2rem;}

  .gu_wrap.index{padding:0 2rem 2rem; margin:-10rem 0 0;}
  .gu_wrap.index .gulist{padding:2rem; box-shadow:15px 15px 30px rgba(0,0,0,0.15);}
 
  .gu_wrap h2{color:#42b983; font-size:1.125rem; font-weight: 700;}
  .gu_wrap h2 i{padding-right:5px;}

  .gulist{ background:#fff; border-radius:10px; padding:0; box-shadow:0 0 0 rgba(0,0,0,0);}
  .gulist ul{display:flex; flex-wrap: wrap; border-width:1px 0 0 1px; border-style:solid; border-color:#ddd; margin:1rem 0 0;}
  .gulist li{width:10%; box-sizing:border-box; border-width:0 1px 1px 0; border-style:solid; border-color:#ddd;}
  .gulist li button{position:relative; display: block; width:100%; height: 3rem; line-height:3rem; text-align:center; border:0 none; background:none; transition:all .3s; white-space: nowrap;}
  .gulist li button::after{content:''; display:block; position:absolute; left:-1px; top:-1px; width:100%; height:100%; border:1px solid #42b983; opacity: 0; transition:all .3s;}
  .gulist li button:hover,
  .gulist li.active{color:#42b983; background:rgba(66,185,131, .1)}
  .gulist li button:hover::after,
  .gulist li.active button::after{opacity: 1;}

  @media only screen and (max-width:1024px) {
    .gulist li{width:20%}
  }

  @media only screen and (max-width:480px) {
    .gulist li{width:25%}
  }

  @media only screen and (max-width:375px) {
    .gulist li{width:33.33%}
  }
</style>